function SVGLogo(props) {

    if (props.isHidden) {
        return <svg display='none' style={{ display: 'none' }}>{props.children}</svg>;
    }

    let file = files.byId[props.id];

    if (!file) return null;

    let width = props.width || file.width || null;
    let height = props.height || file.height || null;
    let viewBox = file.viewBox
        ? file.viewBox.join(' ')
        : '0 0 ' + props.width + ' ' + props.height;

    return props.isHidden
        ? <svg display='none'>{props.children}</svg>
        : (
            <svg
                xmlns='http://www.w3.org/2000/svg'
                version='1.1'
                preserveAspectRatio='xMinYMin'
                width={width}
                height={height}
                viewBox={viewBox}
                className={props.className}
                role={props.role}
                aria-label={props.ariaLabel}
            >
                <use href={'#' + prefix + '-' + props.id} />
            </svg>
        );

}


function SVGSourceLogo(props) {

    return (
        <SVGLogo isHidden={true}>
            <defs>
                {files.allIds.reduce((defs, fileId, fileIndex) => {
                    const file = files.byId[fileId];
                    return defs.concat(
                        <g
                            key={fileIndex}
                            id={prefix + '-' + fileId}
                            dangerouslySetInnerHTML={{ __html: file.data }}
                        >
                        </g>
                    );
                }, [])}
            </defs>
        </SVGLogo>
    );

}


export {
    SVGLogo,
    SVGSourceLogo
}
