import { createGlobalStyle } from 'styled-components';

import firaSans600Woff from './fira-sans-v11-latin/fira-sans-v11-latin-600.woff';
import firaSans600Woff2 from './fira-sans-v11-latin/fira-sans-v11-latin-600.woff2';
import firaSans700Woff from './fira-sans-v11-latin/fira-sans-v11-latin-700.woff';
import firaSans700Woff2 from './fira-sans-v11-latin/fira-sans-v11-latin-700.woff2';
import firaSansItalicWoff from './fira-sans-v11-latin/fira-sans-v11-latin-italic.woff';
import firaSansItalicWoff2 from './fira-sans-v11-latin/fira-sans-v11-latin-italic.woff2';
import firaSansWoff from './fira-sans-v11-latin/fira-sans-v11-latin-regular.woff';
import firaSansWoff2 from './fira-sans-v11-latin/fira-sans-v11-latin-regular.woff2';
import accanthisRegOtf from './accanthis-stdNo2/accanthis-stdNo2-regular.otf';
import accanthisBoldOtf from './accanthis-stdNo2/accanthis-stdNo2-bold.otf';
import accanthisBoldItalicOtf from './accanthis-stdNo2/accanthis-stdNo2-bold-italic.otf';
import accanthisItalicOtf from './accanthis-stdNo2/accanthis-stdNo2-italic.otf';

export const GlobalFonts = createGlobalStyle`
   
    /* fira-sans-regular - latin */
    @font-face {
        font-family: 'Fira Sans';
        font-style: normal;
        font-weight: 400;
        src: local(''),
            url(${firaSansWoff2}) format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
            url(${firaSansWoff}) format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
    }
    /* fira-sans-italic - latin */
    @font-face {
        font-family: 'Fira Sans';
        font-style: italic;
        font-weight: 400;
        src: local(''),
            url(${firaSansItalicWoff2}) format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
            url(${firaSansItalicWoff}) format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
    }
    /* fira-sans-600 - latin */
    @font-face {
        font-family: 'Fira Sans';
        font-style: normal;
        font-weight: 600;
        src: local(''),
            url(${firaSans600Woff2}) format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
            url(${firaSans600Woff}) format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
    }
    /* fira-sans-700 - latin */
    @font-face {
        font-family: 'Fira Sans';
        font-style: normal;
        font-weight: 700;
        src: local(''),
            url(${firaSans700Woff2}) format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
            url(${firaSans700Woff}) format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
    }
    
    /* accanthis-stdNo2-regular  */
    @font-face {
        font-family: 'Accanthis Std No2';
        font-style: normal;
        font-weight: 400;
        src: local(''),
            url(${accanthisRegOtf}) format('opentype');
    }
    /* accanthis-stdNo2-bold  */
    @font-face {
        font-family: 'Accanthis Std No2';
        font-style: normal;
        font-weight: bold;
        src: local(''),
            url(${accanthisBoldOtf}) format('opentype');
    }
    /* accanthis-stdNo2-bold-italic  */
    @font-face {
        font-family: 'Accanthis Std No2';
        font-style: italic;
        font-weight: bold;
        src: local(''),
            url(${accanthisBoldItalicOtf}) format('opentype');
    }
    /* accanthis-stdNo2-italic  */
    @font-face {
        font-family: 'Accanthis Std No2';
        font-style: italic;
        font-weight: 400;
        src: local(''),
            url(${accanthisItalicOtf}) format('opentype');
    }
`
