import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

/**
 * Make sure all pages scroll to top on page load.
 * 
 * @returns null
 */
export const ScrollToTop = () => {
    const { pathname } = useLocation();

    useEffect(() => {
        window.scrollTo(0, 0);
    }, [pathname]);

    return null;
}