import { useEffect } from 'react';
import { useIsLoadingContext } from '../context';

/**
 * Guard function for the pages to set isLoading so that the 
 * header and footer will render accordingly.
 * 
 * @param {Boolean} bool Value of what to set isLoading too
 */
export const PageRefreshDisplayGuard = (bool) => {
    const { setIsLoading } = useIsLoadingContext();
    useEffect(() => {
        setIsLoading(bool);
    }, [bool, setIsLoading]);
}

