import styled from 'styled-components';
import { theme } from '../theme';

export const TableFilter = styled.section`
    display: flex;
    flex-direction: column;
    position: relative;

    &.table-filter--open {
        position: relative;
        top: 0;
        max-height: 5000px;
        opacity: 1;
        transition: max-height 300ms cubic-bezier(1, 0, 0.7, 1),
            opacity 400ms linear;
    }

    &.table-filter--close {
        position: relative;
        top: -9999px;
        max-height: 0;
        opacity: 0;
        transition: max-height 400ms cubic-bezier(0, 1.05, 0, 1),
            opacity 200ms linear, top 500ms 500ms;
    }

    .table-filter__input-row {
        margin: 0.8em auto;
    }

    .table-filter__input-container {
        display: flex;

        @media (max-width: 500px) {
            width: 95%;
        }
    }

    .table-filter__icon--delete-text {
        align-self: center;
        height: 1.5rem;
        width: 1.5rem;

        :hover {
            transform: scale(1.1);
            cursor: pointer;
        }
    }

    .table-filter__icon-container {
        position: absolute;
        top: 0.8em;
        right: 0.2em;

        ::before {
            content: 'Close filter';
            color: ${theme['--clr-gray-600']};
            font-style: italic;
            width: auto;
            position: absolute;
            top: 0.5em;
            left: -5em;
            opacity: 0;
            transition: opacity 300ms ease;
        }

        :hover::before {
            opacity: 1;
        }
    }

    .table-filter__icon--close-table-filter {
        height: 2rem;
        width: 2rem;
        fill: ${theme['--clr-green-400']};

        :hover {
            cursor: pointer;
            fill: ${theme['--clr-gray-700']};
        }
    }

    .table-filter__column-toggle-container {
        display: flex;
        flex-wrap: wrap;
        position: relative;

        > * {
            flex: 1 1 33%;
            min-width: 200px;
            padding: 5px;

            @media (min-width: 1200px) {
                flex: 1 1 25%;
            }
        }
    }

    .table-filter__checkbox {
        -webkit-appearance: none;
        background-color: ${theme['--clr-green-400']};
        margin-right: 1em;
        padding: 0.6rem;
        border-radius: 3px;
        display: inline-block;
        position: relative;
        top: 0.3em;
        box-shadow: 0 5px 7px ${theme['--clr-gray-400']},
            inset 0 0 3px ${theme['--clr-gray-700']};

        :checked {
            background-color: ${theme['--clr-green-300']};
            padding: 0.6rem;
        }

        :checked::after {
            content: '\\2714';
            font-size: 2rem;
            color: ${theme['--clr-gray-700']};
            position: absolute;
            top: -0.4em;
            left: 0;
        }
    }

    .table-filter__checkbox-label {
        color: ${theme['--clr-gray-700']};
        font-size: calc(0.5rem + 0.5vw);

        @media (max-width: 768px) {
            font-size: calc(0.8rem + 0.5vw);
        }
    }

    .table-filter__checkbox:not(:checked) + .table-filter__checkbox-label {
        text-decoration-line: line-through;
        text-decoration-color: ${theme['--clr-red-400']};
        text-decoration-thickness: 3px;
    }

    .table-filter__checkbox--toggle-all{
        position: absolute;
        left: 0;
        bottom: -36px;

        @media(max-width: 500px){
            position: static;
        }
    }
`;
