import React, { createContext, useContext, useState } from 'react';
const ModalMessageContext = createContext();

/**
 * Custom hook for storing modal messages
 *
 * @returns ModalMessageContext
 */
export const useModalMessageContext = () => {
    return useContext(ModalMessageContext);
};

/**
 * Context provider for the message to be displayed on a modal
 *
 * @param {{ children: JSX.Element}} [Props]
 * @returns Context provider for ModalMessage
 */
export const ModalMessageProvider = ({ children }) => {
    const [modalMessage, setModalMessage] = useState({
        heading: '',
        msg: '',
    });

    return (
        <ModalMessageContext.Provider value={{ modalMessage, setModalMessage }}>
            {children}
        </ModalMessageContext.Provider>
    );
};
