import React, { createContext, useContext, useEffect, useState } from 'react';
const SearchParamsContext = createContext();

/**
 * Custom hook for storing the users search params
 *
 * @returns SearchParamsContext
 */
export const useSearchParamsContext = () => {
    return useContext(SearchParamsContext);
};

/**
 * Context provider for the users search params. Sets params to 
 * session storage.
 *
 * @param {{ children: JSX.Element}} [Props]
 * @returns Context provider for SearchParams
 */
export const SearchParamsProvider = ({ children }) => {
    const [searchParams, setSearchParams] = useState({
        zipCode: '',
        distance: '',
    });

    useEffect(() => {
        const sessionStore = sessionStorage.getItem('searchParams');
        sessionStore
            ? setSearchParams(JSON.parse(sessionStore))
            : setSearchParams({
                  zipCode: '',
                  distance: '',
              });
    }, []);

    useEffect(() => {
        sessionStorage.setItem('searchParams', JSON.stringify(searchParams));
    }, [searchParams]);

    return (
        <SearchParamsContext.Provider value={{ searchParams, setSearchParams }}>
            {children}
        </SearchParamsContext.Provider>
    );
};
