import styled from 'styled-components';
import { theme } from '../theme';

export const Input = styled.input`
    font-family: inherit;
    font-size: ${ props => (props.parent === 'search-bar' ? '3rem' : '1.2rem') };
    text-align: ${ props => (props.parent === 'search-bar' ? 'center' : 'inherit') };
    color: ${theme['--clr-gray-700']};
    font-weight: 500;
    background-color: transparent;
    width: ${ props => (props.parent === 'search-bar' ? '70%' : 'inherit') };
    border: none;
    border-bottom: solid ${theme['--clr-brown-400']} ${props => (props.parent === 'search-bar' ? '5px' : '2px')};
    caret-color: ${theme['--clr-brown-400']};

    &::placeholder {
        font-size: 1rem;
        text-align: center;
    }

    &:focus {
        outline: none;
    }

    /* Remove the BG color on autofills and set text styling */
    &:-webkit-autofill,
    :-webkit-autofill:hover,
    :-webkit-autofill:focus {
        -webkit-text-fill-color: inherit !important;
        -webkit-box-shadow: 0 0 0px 1000px transparent inset;
        transition: background-color 5000s ease-in-out 0s;
    }

    &:-webkit-autofill::first-line:not(.input__contact-page) {
        font-family: ${theme['--ff-primary']};
        font-size: 3rem;
        color: ${theme['--clr-gray-700']};
    }

    @media(max-width: 500px){
        width: ${ props => (props.parent === 'search-bar' ? '70%' : 'inherit') };
    }
`