import React from 'react';
import { Redirect, Route, Switch, useLocation } from 'react-router-dom';
import { GlobalFonts } from './fonts';
import { GlobalStyle } from './theme';
import {
    ContactPage,
    FourOhFourPage,
    AboutPage,
    ReportPage,
    ResultsPage,
    SearchPage,
} from './pages';
import { useDispoDataContext, useIsLoadingContext } from './context';
import { Footer, Header } from './components';
import { ScrollToTop } from './utils';
import { SVGSourceIcon, SVGSourceLogo } from './images';

function App() {
    const location = useLocation();
    const { dispoData } = useDispoDataContext();
    const { isLoading } = useIsLoadingContext();
    return (
        <>
            <GlobalFonts />
            <GlobalStyle />
            <SVGSourceIcon />
            <SVGSourceLogo />
            <ScrollToTop />
            {(dispoData || !isLoading) && location.pathname !== '/' ? (
                <Header />
            ) : null}
            <main style={{ paddingBottom: '12rem' }}>
                <Switch>
                    <Route
                        exact
                        path={['/', '/search']}
                        component={SearchPage}
                    />
                    {/* <Route
                        exact
                        path={'/results/:zip/:distance'}
                        component={ResultsPage}
                    /> */}
                    {/* <Route exact path={'/about'} component={AboutPage} />
                    <Route exact path={'/report'} component={ReportPage} />
                    <Route exact path={'/contact'} component={ContactPage} /> */}
                    <Route exact path={'/404'} component={FourOhFourPage} />
                    <Redirect to={'/404'} />
                </Switch>
            </main>
            {/* {(dispoData || !isLoading) && location.pathname !== '/' ? (
                <Footer />
            ) : null} */}
        </>
    );
}

export default App;
