import axios from 'axios';
const dev = 'http://localhost:3000';
const prod = 'https://server.flowerleed.com';
const url = `${process.env.NODE_ENV === 'development' ? dev : prod}`;


/**
 * API calls to the backend
 */
export const api = {
    async getDispos(zip, range) {
        return await axios.get(`${url}/api/search/${zip}/${range}`);
    },
    async sendEmail(emailData) {
        return await axios.post(`${url}/api/contact/`, emailData);
    },
    async reCAPTCHAVerify(userResponseToken) {
        return await axios.post(`${url}/api/recapverify/${userResponseToken}`);
    },
};
