import styled from 'styled-components';
import { theme } from '../theme';

export const Table = styled.section`
    position: relative;

    .table {
        max-height: 75vh;
        width: fit-content;
        max-width: 90vw;
        margin: 0 auto;
        background: ${theme['--clr-gray-400']};
        border: 1px solid ${theme['--clr-gray-100']};
        border-radius: 10px;
        box-shadow: 0 0 20px ${theme['--clr-gray-400']};

        @media (max-width: 768px) {
            width: 95vw;
            width: fit-content;
        }

        .tr {
            :nth-child(even) {
                background-color: hsl(0, 0%, 95%);
                width: fit-content;
            }

            :nth-child(odd) {
                background-color: hsl(0, 0%, 100%);
                width: fit-content;
            }

            :last-child {
                .td {
                    border-bottom: 3px solid ${theme['--clr-gray-100']};
                }
            }
        }

        .th {
            font-family: ${theme['--ff-secondary']};
            font-size: 1.2rem;
            line-height: 1.4rem;
            text-align: center;
            background-color: ${theme['--clr-green-400']} !important;
            color: hsl(0, 0%, 100%);
        }

        .th,
        .td {
            padding: 0.5em;
            border-bottom: 1px solid ${theme['--clr-gray-100']};
            border-right: 1px solid ${theme['--clr-gray-100']};

            :first-child {
                background-color: hsl(0, 0%, 100%);
                /* white-space: nowrap; */
            }

            :last-child {
                border-right: 3px solid ${theme['--clr-gray-100']};
            }
        }

        .td {
            :not(:first-child) {
                text-align: right;
            }

            :hover {
                background-color: ${theme['--clr-gray-100']};
            }
        }

        &.sticky {
            overflow: scroll;

            /* Scrollbar styling */
            /* Firefox */
            scrollbar-color: ${theme['--clr-green-300']} ${theme['--clr-gray-100']};

            /* Chrome, Safari */
            ::-webkit-scrollbar,
            ::-webkit-scrollbar-corner {
                background-color: ${theme['--clr-gray-100']};
            }

            ::-webkit-scrollbar-track {
                box-shadow: inset 0 0 6px hsla(0, 0%, 0%, 0.3);
                -webkit-box-shadow: inset 0 0 6px hsla(0, 0%, 0%, 0.3);
            }

            ::-webkit-scrollbar-thumb {
                height: 10px;
                background-color: ${theme['--clr-green-300']};
                border: 1px solid ${theme['--clr-green-500']};
                border-radius: 10px;
                box-shadow: inset 0 0 7px hsla(0, 0%, 0%, 0.7),
                    0 5px 15px hsla(0, 0%, 0%, 0.7);
                -webkit-box-shadow: inset 0 0 7px hsla(0, 0%, 0%, 0.7),
                    0 5px 15px hsla(0, 0%, 0%, 0.7);
            }

            .header {
                width: fit-content;
                position: -webkit-sticky;
                position: sticky;
                top: 0;
                z-index: 1;
                box-shadow: 0px 3px 3px hsl(0, 0%, 80%);
            }

            .body {
                position: relative;
                z-index: 0;
            }

            [data-sticky-td] {
                position: -webkit-sticky;
                position: sticky;
            }

            [data-sticky-last-left-td] {
                box-shadow: 2px 0px 3px hsl(0, 0%, 80%);

                @media (max-width: 768px) {
                    min-width: 150px !important;
                    max-width: 150px !important;
                    white-space: normal;
                }
            }

            [data-sticky-first-right-td] {
                box-shadow: -2px 0px 3px hsl(0, 0%, 80%);
            }
        }

        a:hover {
            color: ${theme['--clr-bg-primary']};
        }
    }
`;
