import React, { createContext, useContext, useState } from 'react';
const DispoDataContext = createContext();

/**
 * Custom hook for storing the results of the dispo query results
 * 
 * @returns DispoDataContext 
 */
export const useDispoDataContext =() => {
    return useContext(DispoDataContext);
}

/**
 * Context provider for dispoData returned from the DB
 * 
 * @param {{ children: JSX.Element}} [Props]
 * @returns Context provider for dispoData
 */
export const DispoDataProvider = ({ children }) => {
    const [dispoData, setDispoData] = useState();

    return (
        <DispoDataContext.Provider value={{ dispoData, setDispoData }}>
            { children }
        </DispoDataContext.Provider>
    );
} 
 