import  { useEffect } from 'react';
import { useDispoDataContext, useIsLoadingContext } from '../context';
// import { SearchBar } from '../components';
import * as S from '../styled_components';
import { SVGLogo } from '../images';

export const SearchPage = () => {
    const { setDispoData } = useDispoDataContext();
    const { setIsLoading } = useIsLoadingContext();

    useEffect(() => {
        setDispoData();
        setIsLoading(false);
    }, []);

    return (
        <S.SearchPage>
            <SVGLogo
                id='logo-nobg'
                className='search-page__svg'
                height='500'
                width='500'
                role='img'
                ariaLabel='flowerleed logo'
            />

            {/* <p className='search-page__p-tagline'>
                <em>A place for PA MMJ Patients</em>
            </p> */}
            <p className='search-page__p-tagline'>
                <em>Due to unforeseen circumstances, FlowerLeed is no more.</em>
            </p>

            {/* <SearchBar /> */}
        </S.SearchPage>
    );
};
