import React, { createContext, useContext, useState } from 'react';
const IsLoadingContext = createContext();

/**
 * Custom hook for storing if a page is loading
 * 
 * @returns IsLoadingContext 
 */
export const useIsLoadingContext = () => {
    return useContext(IsLoadingContext);
}

/**
 * Context provider if a page is loading
 * 
 * @param {{ children: JSX.Element}} [Props]
 * @returns Context provider for isLoading
 */
export const IsLoadingProvider = ({ children }) => {
    const [isLoading, setIsLoading] = useState(true);

    return (
        <IsLoadingContext.Provider value={{ isLoading, setIsLoading }}>
            { children}
        </IsLoadingContext.Provider>
    );
}
