import React, { useState } from 'react';
import { useModalMessageContext } from '../context/';
import { Modal, Spinner } from '../components/';
import { api, PageRefreshDisplayGuard } from '../utils/';
import * as S from '../styled_components/';
import ReCAPTCHA from 'react-google-recaptcha';
const reCAPSiteKey = '6Lfx2gwbAAAAAER9nptEBR8oaZ4nZ9XKyXAz5aBR';

export const ContactPage = () => {
    /** Sets isLoading to false */
    PageRefreshDisplayGuard(false);

    const [userName, setUserName] = useState('');
    const [userEmail, setUserEmail] = useState('');
    const [userMessage, setUserMessage] = useState('');
    const [showModal, setShowModal] = useState(false);
    const [showBtn, setShowBtn] = useState(false);
    const [showSpinner, setShowSpinner] = useState(false);

    const { setModalMessage } = useModalMessageContext();

    const emailSuccessMsg = {
        heading: 'Success!',
        msg: 'Thanks for reaching out. We will try to respond shortly.',
    };

    const emailFailureMsg = {
        heading: 'NOOO!!',
        msg: 'Looks like there was issue. Please email admin@flowerleed.com.',
    };

    /**
     * Setup email template per state of users inputs
     * @type {Object}
     */
    const templateParams = {
        from_name: userName,
        reply_to: userEmail,
        message: userMessage,
    };

    /**
     * Set the state of the user inputs
     *
     * @param {Object} e event object
     * @returns {string} the users input to state
     */
    const handleInputChange = (e) => {
        const { name, value } = e.target;

        switch (name) {
            case 'name':
                setUserName(value);
                break;
            case 'email':
                setUserEmail(value);
                break;
            case 'message':
                setUserMessage(value);
                break;
            default:
                break;
        }
    };

    /**
     * Handles user interaction & verification w/ ReCAPTHCHA
     *
     * @param {string} userResponseToken - token sent to Google for verify
     * @returns {Object} - Status of verification
     */
    const captchaOnChange = async (userResponseToken) => {
        const response = await api.reCAPTCHAVerify(userResponseToken);

        return response.data.success
            ? (setShowBtn(true), console.log('You are human!'))
            : (setShowBtn(false), console.log('Begone bot!!'));
    };

    /**
     * On form submit call the sendEmail function and display Modal w/
     * appropriate message.
     *
     * @param {Object} e  event object
     */
    const handleFormSubmit = async (e) => {
        try {
            e.preventDefault();
            setShowSpinner(true);
            const results = await api.sendEmail(templateParams);
            results.data === 'OK'
                ? setModalMessage(emailSuccessMsg)
                : setModalMessage(emailFailureMsg);
            document.querySelector('body').classList.add('body--dim');
            setShowModal(true);
            setShowSpinner(false);
        } catch (err) {
            setModalMessage(emailFailureMsg);
            document.querySelector('body').classList.add('body--dim');
            setShowModal(true);
            setShowSpinner(false);
        }
    };

    return (
        <S.Pages className='pages-contact__container'>
            <Modal showModal={showModal} setShowModal={setShowModal} />

            <div className='pages-contact__title'>
                <h1>Contact</h1>
            </div>
            <S.Form className='form' onSubmit={handleFormSubmit}>
                <div className='form-group form__input--name'>
                    <label htmlFor='name'>Name</label>
                    <S.Input
                        className='input__contact-page'
                        type='text'
                        name='name'
                        id='name'
                        onChange={handleInputChange}
                        required
                    />
                </div>
                <div className='form-group form__input--email'>
                    <label htmlFor='email'>Email</label>
                    <S.Input
                        className='input__contact-page'
                        type='email'
                        name='email'
                        id='email'
                        onChange={handleInputChange}
                        required
                    />
                </div>

                <div className='form-group form__textarea--message'>
                    <label htmlFor='message'>Message</label>
                    <textarea
                        name='message'
                        id='message'
                        rows='5'
                        onChange={handleInputChange}
                    />
                </div>

                <ReCAPTCHA
                    className='form__recaptcha fade-in'
                    sitekey={reCAPSiteKey}
                    onChange={captchaOnChange}
                    render='explicit'
                />

                <div className='form__button-container'>
                    {showBtn ? (
                        <S.Button
                            type='submit'
                            className='btn__contact-page form__button'>
                            Send
                        </S.Button>
                    ) : null}

                    {showSpinner ? <Spinner /> : null}
                </div>
            </S.Form>
        </S.Pages>
    );
};
